<template>
	<div :style='alertStyle'>
		<div :style='alertAll'>
			<!-- 容器 -->
			<div class="alert-head">
				<span class="head-title" v-text='$store.state.addTitle'></span>
				<img   src="../../assets/images/x.png" alt="" />
			</div>
			<ul class="alert-body">
				<li>
					<span>用户名：</span>
					<input type="text" v-model='userInfor.userName'/>
				</li>
				<li>
					<span>姓名：</span>
					<input type="text" v-model='userInfor.turename'/>
				</li>
				<li>
					<span>手机号：</span>
					<input type="text" v-model='userInfor.phone'/>
				</li>
				<li>
					<span>权限选择：</span>
					<select v-model='userInfor.power'>
						<option >系统管理员</option>
						<option >课程管理员</option>
					</select>
				</li>
			</ul>
			<div style="padding-top:15px;">
				<button @click='enter' class="btn btn-success" >确定</button>
				<button @click='close' class="btn btn-info" >取消</button>
			</div>
		</div>
	</div>
</template>

<script>
	import store from'../../vuex/adminStore'
	export default {
		store,
		data(){
			return{
				alertStyle:{
					'width':window.innerWidth+'px',
					'height':window.innerHeight+'px',
					'position':'absolute',
					'top':0,
					'left':0,
					'background':'rgba(255,255,255,0.8)'
				},
				alertAll:{
					'position':'relative',
					'width':'410px',
					'height':'250px',
					'top':window.innerHeight/2 -250/2 +'px',
					'left':window.innerWidth/2 -410/2 +'px',
					'background':'#fff',
					'border':'5px solid red'
				},
			}
		},
		methods:{
			close(){  // 弹窗组件
				this.$store.commit('setAddBtnFlag','none')
				//清空弹窗内容
				this.$store.commit('clearInfor')
			},
			enter(){
				//注册功能 和编辑功能  ==》区别   注册没有tokenId
				// 根据两者的区别 选择请求不同的接口
	//  tokenId  在 state.userInfor 初始话。mutations里面 就有设置了			
				var url='';
				// 准备的数据
				var data={
					userName:this.userInfor.userName, //adminStore里面的数据
					turename:this.userInfor.turename,
					phone:this.userInfor.phone,
					power:this.userInfor.power,
//					password:'123456'  // 编辑操作的时候 没有password的操作
				};
				if(this.userInfor.tokenId){ //调用编辑功能
					data.tokenId=this.userInfor.tokenId;
					url='/VueHandler/AdminHandler?action=update'
				}else{ //注册操作
					data.password='123456';
					url='/VueHandler/AdminLoginAndRegHandler?action=add'
				}
				
				var that=this;
				this.$axios({
					url:url,
					method:"post",
					data:data
				}).then(function(res){
					console.log(res);
				// 1.如果注册成功  关闭弹窗组件   2.清空弹窗组件的内容	 3.注册完成，重新加载数据
				if(res.data.success){
					alert('注册成功')
					that.close();
				//  2.清空弹窗组件的内容 因为 弹窗组件里面的数据 绑定的 store.state里面的值
				//   修改 state里面的值，只能通过 mutations
				that.$store.commit('clearInfor')
				that.$store.state.tableList.getListData();
				}else{
					alert('注册失败')
				}
				})
				
			}
		},
		computed:{
			// ...mapState(['userInfor'])
			userInfor(){
				return this.$store.state.userInfor
			}
		}
	}
</script>

<style scoped>
  .alert-head {
    height: 30px;
    background: #4F66B3;
    margin-bottom: 10px

  }

  .head-title {
    color: white;
    line-height: 30px;
    font-size: 14px;
    display: inline-block;
    float: left
  }

  .alert-head img {
    display: inline-block;
    float: right;
    margin-top: 5px;
    margin-right: 5px;
    cursor: pointer;
  }

  ul.alert-body li {
    display: block;
    margin-bottom: 10px
  }

  ul.alert-body li span {
    display: inline-block;
    width: 80px;
    text-align: right;
  }

  ul.alert-body li input {
    width: 200px;

  }

  ul.alert-body li select {
    width: 204px;
    height: 24px
  }

  .btn {
    width: 80px;
    height: 30px;
    line-height: 20px;
    border: none;
    color: #fff;
    text-align: center;
    cursor: pointer;
  }

  .btn-info {
    background: #3cadcf;
    float: right;
    margin-right: 60px;

  }

  .btn-info:hover {
    background: #1f8fb0;
  }

  .btn-success {
    background: #5cb85c;
    float: left;
    margin-left: 60px;
  }

  .btn-success:hover {
    background: #449d44;
  }
</style>	






